import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			cashList: [{
					name: '正常',
					id: '2'
				},
				{
					name: '未开始',
					id: '1'
				},
				{
					name: '已使用',
					id: '3'
				},
				{
					name: '已过期',
					id: '4'
				}
			],
			list: [],
			page: 1,
			num: 10,
			bitmap: true,
			loading: true,

			activeTab: -1,

			activeTab: 0,
			orderType: 2,
		};
	},
	methods: {

		tabClick(data){
			this.activeTab = data;
			this.orderType = this.cashList[data].id;
			this.list = [];
			this.page = 1;
			this.myCash();
		},

		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		changeFirst(index) {
			console.log(index)
			this.page = 1;
			this.list = [];
			this.orderType = this.cashList[index].id;
			this.myCash();
		},
		myCash() {
			var that = this;
			
			if (that.page != 1) {
				return;
			}
			that.loading=true;
			that.bitmap=true;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var {
				num,
				page,
				orderType
			} = that;
			var obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				num: num,
				page: page,
				timeStamp: timeStamp,
				type: orderType,
				sign: sign
			};
			rs.getRequests('couponsList', data, res => {
				let {
					data
				} = res;

				if (data.code == 200) {
					let list = data.data.list;
					this.list = list;
					if (list.length == 0) {
						this.bitmap = false;
						this.loading = '空';
					} else {
						this.bitmap = true;
						if (list.length <10) {
							this.loading = false;
						}else{
							this.loading=true;
						}
					}
				}
			});
		},
		detailPage(id) {
			uni.navigateTo({
				url: 'cashdetail?id=' + id
			})
		}
	},
	onShow() {
		this.myCash();
	},
	onReachBottom() {
		var that = this;
		var timeStamp = Math.round(new Date().getTime() / 1000);
		var {
			num,
			page,
			orderType
		} = that;
		var obj = {
			appid: appid,
			timeStamp: timeStamp,
		}
		var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		var data = {
			appid: appid,
			num: num,
			page: page + 1,
			timeStamp: timeStamp,
			type: orderType,
			sign: sign
		}
		rs.getRequests("couponsList", data, (res) => {
			let {
				data
			} = res
			if (data.code == 200) {
				if (res.data.data.list == '') {
					that.loading = false
				} else {
					that.list.push(...res.data.data.list);
					that.page += 1;
					that.loading = true;
				}
			}
		})
	}
};
